.slick_header {
    margin-bottom: 10px;
}
.slick-list {
    overflow: visible!important;
}
.slick-slider {
    margin-bottom: 60px;
    min-height: 200px;
}
main > div:last-child .slick-slider{
    margin-bottom: 0;
}
.slick-track {
    min-width: 100%!important;
    display: flex!important;
}
.slick-slide {
    padding: 0 15px;
}
.slick-slide:first-child {
    padding-left: 0;
}
.slick-slide div {
    outline: 0;
}
.custom_slick {
    background-position: top!important;
    background-size: cover!important;
    background-repeat: no-repeat;
    background: #222;
    color: #fff;
    height: 200px;
    position: relative;
    border-radius: 5px;
    cursor: pointer;
    transition: all .3s ease-in-out;
}
.custom_slick:hover {
    filter: brightness(120%);
    filter: saturate(1.2);
    border-radius: 0;
    transform: scale(1.05);
}
.custom_slick_title {
    position: absolute;
    width: 100%;
    text-align: center;
    bottom: 15px;
    padding: 0 15px;
}