header {
    height: 50vh;
    width: 100%;
    text-align: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    position: relative;
}
.header_content {
    position: absolute;
    bottom: 50px;
    left: 50%;
    width: 700px;
    padding: 0 15px;
    transform: translateX(-50%);
}
.header_content h2 {
    margin-bottom: 20px;
}
.header_content a {
    background: #553C9A;
    padding: 10px;
    border-radius: 3px;
    color: #fff;
    text-decoration: none;
    transition: all .1s ease-in-out;
}
.header_content a:hover {
    filter: brightness(1.1);
}