#Search {
    padding-top: 76px;
    min-height: 100vh;
}
#Search h1 {
    font-size: 1.8rem;
    font-weight: normal;
    text-align: center;
    margin: 20px 0;
}
.media_wrapper {
    margin: 15px;
    background-position: center!important;
    background-size: cover!important;
    background-repeat: no-repeat;
    background: #222;
    color: #fff;
    height: 200px;
    position: relative;
    border-radius: 5px;
    cursor: pointer;
    transition: all .3s ease-in-out;
    width: calc((100% - 30px * 5) / 5);
    float: left;
}
.media_wrapper:hover {
    filter: brightness(120%);
    filter: saturate(1.2);
    border-radius: 0;
    transform: scale(1.05);
}
.media_title {
    position: absolute;
    width: 100%;
    text-align: center;
    bottom: 15px;
    padding: 0 15px;
}
@media (max-width: 1300px) {
	.media_wrapper {
        width: calc((100% - 15px * 2 * 4) / 4);
    }
}
@media (max-width: 1000px) {
	.media_wrapper {
        width: calc((100% - 15px * 2 * 3) / 3);
    }
}
@media (max-width: 700px) {
	.media_wrapper {
        width: calc((100% - 15px * 2 * 2) / 2);
    }
}
@media (max-width: 480px) {
	.media_wrapper {
        width: calc((100% - 15px * 2 * 1) / 1);
    }
}