#Tv {
	padding-top: 76px;
	overflow-x: hidden;
	min-height: 100vh;
}
#Tv header {
	position: relative;
	padding: 20px 15px;
	min-height: 400px;
	text-align: left;
}
#Tv #header_background {
	z-index: -2;
	top: 0;
	left: 0;
	position: absolute;
	height: 100%;
	width: 100%;
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
	filter: blur(20px) brightness(60%);
}
#Tv #header_container {
	display: flex;
	margin: auto;
	max-width: 800px;
	height: 100%;
}
#Tv #poster {
	flex: auto 1;
	float: left;
	width: 185px;
	position: relative;
}
#Tv #header_content {
	flex: auto 1;
	padding-left: 15px;
	float: left;
	width: calc(100% - 185px);
	position: relative;
	text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.4);
}
#Tv #header_content p,
#Tv #header_content small {
	line-height: 1.6rem;
	text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.8);
}
#Tv #poster img {
	box-shadow: 0px 3px 20px rgba(0, 0, 0, 0.5);
}
#Tv #poster img,
#header_content_wrapper {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
}
#Tv main {
	max-width: 700px;
	margin: auto;
}
.dv-star-rating {
	font-size: 24px;
}
#Tv #mobile_content {
	position: relative;
	display: none;
	margin-bottom: 60px !important;
}
#Tv #mobile_content h1 {
	text-align: center;
}
#Tv #mobile_content .dv-star-rating {
	margin-bottom: 60px;
	left: 50%;
	transform: translateX(-50%);
}
#Tv .slick_header {
	margin-top: 80px;
}
.load {
	opacity: 0;
	transition: all 0.6s ease-in-out;
}
@media (max-width: 700px) {
	#Tv header {
		min-height: 400px;
	}
	#header_content {
		display: none;
	}
	#Tv #poster img {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translateY(-50%) translateX(-50%);
	}
	#Tv #mobile_content {
		display: block;
	}
}
