#Navbar {
    transition: all .3s ease-in-out;
    padding: 20px 0;
    width: 100%;
    position: fixed;
    height: 76px;
    overflow-y: hidden;
    z-index: 1;
    background: #000;
    box-shadow: 0 3px 20px black;
}
nav ul {
    float: left;
    top: 50%;
    position: absolute;
    transform: translateY(-50%);
}
nav a {
    margin-left: 20px;
    color: #fff;
    text-decoration: none;
}
nav a:first-child {
    margin-left: 0;
}
nav li {
    display: inline;
}
#hamburger {
    top: 50%;
    transform: translateY(-50%);
    right: 5%;
    position: absolute;
    display: none;
    cursor: pointer;
}
@media (max-width: 700px) {
    .active_menu {
        height: 150px!important;
    }
    nav ul {
        display: block;
        top: 38px;
    }
    #hamburger {
        display: block;
        top: 38px;
    }
    #searchbar {
        float: left!important;
        margin-top: 70px;
        opacity: 0;
        transition: opacity .3s ease-in-out;
    }
	.active_menu #searchbar {
        opacity: 1;
        display: block!important;
    }
}